.main-bg{
    display: flex;
    justify-content: right;
    height: 300px;
    background-image: url('../public/bg.png');
    background-size: cover;
    background-position: center;
}

*{
    text-align: center;
}

.end{
    opacity: 0;
    transition: opacity 0.5s;
}

.start{
    opacity: 1;
    
}

.recent{
    border-radius: 20px;
    margin: 20px;
    width: 100px;
    height: 300px;
    background-color: darkgray;
    position: fixed;
}

.recent img{
    margin-top: 20px;
    width: 80px;
}

